
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import {getCookie} from '@/tool/common.js';
import Drive from '@/tool/classFactory/drive.js';
export default {
directives: {
TransferDom
},
components: {
Confirm,
Group,
Cell,
Popup,
XButton,
XSwitch,
CheckIcon,
Checklist
},
data () {
return {
clientHeight:'',
tips: '技巧讲解',
answer: '浏览实际情况请绕道而行。',
showcancel: true,
cancel: '重听',
congfirm:'关闭',
show: false,
popup: false,
msg: 'Hello World!',
demo1: false,
demo2: true,
commonList: [ 'China', 'Japan', 'America' ],
radioValue: [],
radioOn:true,
params:{
'sessionId':''
},
questionList:[],
question:{},
questionIndex: 0,
count: 0,
questionType: '',
choosed: 3,
params02:{
'type':'2',
'sessionId':''
},
params03:{
'type':'2',
'index':'',
'qid':'',
'sessionId':''
},
examInfo:{

},
time:2700,
btntxt:'',
noFinish: 0,
finish:false,
showTipsImg:true
}
},
mounted(){
const that =this;

this.clientHeight =   `${document.documentElement.clientHeight}`          

that.changeFixed(this.clientHeight)
},
created () {
this.params03.sessionId = this.params02.sessionId = this.params.sessionId = getCookie('sessionId')
this.getErrorQuestionList()


},
methods: {
timer: function () {
if (this.time > 0) {
this.time--;

var m = '';
var s = '';
m = Math.floor(this.time/60%60);
m < 10&&(m='0'+m);
s = Math.floor(this.time%60);
s < 10&&(s='0'+s);
this.btntxt = m + ':' + s

setTimeout(this.timer, 1000);
} else {
this.time = 0;
this.btntxt = "获取验证码";
this.disabled = false;
}
},
changeFixed(clientHeight) {                        //动态修改样式

this.$refs.homePage.style.height = clientHeight + 'px';

},
getErrorQuestionList (e){
const that = this;
Drive.prototype.getErrorQuestionList(that.params).then(res => {
let list = res.data.data || [];
that.questionList = list
that.question = that.questionList[that.questionIndex]
that.count = list.length
that.setquestionType()
console.log(that.question)
});
},
setquestionType(){
const that = this
console.log('setquestionType')
for(var idx in that.question.optionList){
that.question.optionList[idx].key = that.question.optionList[idx].id
that.question.optionList[idx].value = that.question.optionList[idx].optionContent
}

if(that.question.type == 3){
if(that.question.isRight != 0){
for(var idx in that.question.optionList){
if(that.question.optionList[idx].id == that.question.chooseId){
that.choosed = idx
break
}
}
}else {
that.choosed = 3
}

}

if(that.question.type == 1){
if(that.question.isRight != 0){
for(var idx in that.question.optionList){
if(that.question.optionList[idx].id == that.question.chooseId){
that.question.optionList[idx].choosed = true
}else {
that.question.optionList[idx].choosed = false
}
}
}else {
that.radioValue = []
}
console.log(that.choosed)
}
if(that.question.type == 1){
that.questionType = '单选题'
}
if(that.question.type == 2){
that.questionType = '多选题'
}
if(that.question.type == 3){
that.questionType = '判断题'
}
},
getExamInfo (e){
const that = this;
Drive.prototype.getExamInfo(that.params02).then(res => {
let list = res.data.data || {};
that.examInfo = list
});
},
onQuestionTap (item,index){
console.log(index)
const that = this
console.log(this.questionList)
that.radioOn = false
this.question = this.questionList[index]
that.setquestionType()
},
judgeQuestion (e){
const that = this;
Drive.prototype.judgeQuestion(that.params03).then(res => {
let list = res.data.data || {};
that.questionList = list.examDto.questionDtoList












});
},
judgeAnswer (e){

},
onChooseTap (e){
const that = this;
that.choosed = e
that.params03.index = that.questionIndex;  
that.params03.qid = that.question.id;           
that.params03.optionId = that.question.optionList[e].id;      

this.judgeQuestion()
},
change (e){
console.log('change')
const that = this
that.params03.index = that.questionIndex;  
that.params03.qid = that.question.id;           


console.log(that.radioValue.length)
if(that.question.type == 1 && that.radioValue.length != 0 && that.radioOn){
that.params03.optionId = that.radioValue[0]      
console.log(that.params03)
this.judgeQuestion()
}else {
if(that.radioValue.length != 0){
that.radioOn = !that.radioOn
}
}
},
onNextTap (){
const that = this
if(that.count > that.questionIndex+1){
that.question = that.questionList[that.questionIndex+1]
that.questionIndex++
that.setquestionType()
console.log(that.question)
console.log(that.questionList)
}else {
that.showTipsImg = false
that.showTips()
that.finish = true
}
},
onLastTap(){
const that = this
if(that.questionIndex > 0){
--that.questionIndex
that.question = that.questionList[that.questionIndex]
that.radioOn = false
that.setquestionType()
console.log(that.question)
}else {
console.log('first one')
}

},
onCheckTap(){
const that = this
that.popup = true
that.radioOn = false
this.getExamInfo()
},
showTips(){
const that = this
that.tips = '提示'
that.answer = '是否结束错题回顾？'
that.cancel = '取消'
that.congfirm='确认'
that.show = true
},
onHidePopupTap(){
console.log('hide')
this.popup = false
this.radioOn = true
},
Popuphide(){
console.log('hide')
this.radioOn = true
},
onSkillsTap(){

const that = this
that.showcancel = false
that.tips = ''
that.answer = `${that.question.helpContent}`
that.cancel = '再想想'
that.congfirm= '确定'
that.show = true
},
onCancel () {
console.log('on cancel')
this.showTipsImg = true
},
onConfirm (msg) {
this.showTipsImg = true
console.log('on confirm')
const that = this
if(that.finish){
that.$router.push('../index')
}
},
onHide () {
console.log('on hide')
},
onShow () {
console.log('on show')
}
}
}



